import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { of, Subject } from 'rxjs';
import {
  catchError,
  debounceTime,
  distinctUntilChanged,
  share,
  switchMap,
  tap,
} from 'rxjs/operators';
import { ApiService } from '../../../shared/api.service';

@Component({
  selector: 'app-categoryselect',
  templateUrl: './categoryselect.component.html',
  styleUrls: ['./categoryselect.component.css'],
})
export class CategoryselectComponent implements OnInit {
  @Input()
  values;
  @Output()
  valuesChange: EventEmitter<any> = new EventEmitter();

  categorySearch$ = new Subject<string>();
  categories$;
  loading = false;
  selectedcategory;

  constructor(private api: ApiService) {}

  ngOnInit() {
    this.categories$ = this.categorySearch$.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      tap(() => (this.loading = true)),
      switchMap((term: string) =>
        this.api.get(`/api3/categories?name=${term}`).pipe(
          catchError(() => of([])), // empty list on error
          tap(() => (this.loading = false))
        )
      ),
      share()
    );

    setTimeout(() => {
      this.categorySearch$.next('');
    });
  }

  onChange(events) {
    this.valuesChange.emit(events);
  }

  onClear() {
    this.valuesChange.emit(null);
  }
}
