import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { of, Subject } from 'rxjs';
import {
  catchError,
  debounceTime,
  distinctUntilChanged,
  share,
  switchMap,
  tap,
} from 'rxjs/operators';
import { ApiService } from '../../../shared/api.service';

@Component({
  selector: 'app-modifiergroupselect',
  templateUrl: './modifiergroupselect.component.html',
  styleUrls: ['./modifiergroupselect.component.css'],
})
export class ModifiergroupselectComponent implements OnInit {
  @Input()
  values;
  @Output()
  valuesChange: EventEmitter<any> = new EventEmitter();

  modifiergroupSearch$ = new Subject<string>();
  modifiergroups$;
  loading = false;
  selectedmodifiergroups;

  constructor(private api: ApiService) {}

  ngOnInit() {
    this.modifiergroups$ = this.modifiergroupSearch$.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      tap(() => (this.loading = true)),
      switchMap((term: string) =>
        this.api.get(`/api3/modifiergroups?name=${term}`).pipe(
          catchError(() => of([])), // empty list on error
          tap(() => (this.loading = false))
        )
      ),
      share()
    );

    setTimeout(() => {
      this.modifiergroupSearch$.next('');
    });
  }

  addModifiergroup = async name => {
    this.loading = true;
    const newModifiergroup = await this.api
      .post(`/api3/modifiergroups`, {
        code: Date.now().toString(36),
        name,
        modifiergroup: [],
      })
      .toPromise();
    this.loading = false;
    return newModifiergroup;
  };

  onChange(events) {
    this.valuesChange.emit(events);
  }
}
