import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ArticleService } from '../../../article/article.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { ApiService } from 'src/app/shared/api.service';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-articles',
  templateUrl: './articles.component.html',
  styleUrls: ['./articles.component.css']
})
export class ArticlesComponent implements OnInit {

  searchTerm = new FormControl();
  articles: any = [];
  constructor(
    private http: HttpClient,
    private router: Router,
    private activatedroute: ActivatedRoute,
    private articleService: ArticleService,
    private api: ApiService
  ) { }

  ngOnInit() {
    this.getArticles().subscribe((res) => {
			console.log("TCL: ArticlesComponent -> ngOnInit -> res", res)
      this.articles = res;
      this.articleService.setArticles(JSON.stringify(res));
    });
  }

  getArticles(): Observable<any> {
    return this.http.get<any>('/api3/articles');
  }

  createArticle() {
    this.router.navigate(['new'], {
      relativeTo: this.activatedroute,
    });
  }

  articlePreview(articleIndex: number) {
    this.articleService.setArticleHtmlText(this.articles[articleIndex].content);
  }

  async setTopContent(articleIndex: number) {
    let title = this.articles[articleIndex].title;
    if (title[title.length - 1] !== ' ') {
      title += ' ';
    } else {
      title = title.substring(0, title.length - 1);
    }
    const updateArticle = {
      id: this.articles[articleIndex].id,
      title: title,
      previewImage: this.articles[articleIndex].previewImage,
      content: this.articles[articleIndex].content,
      startDate: this.articles[articleIndex].startDate,
      endDate: this.articles[articleIndex].endDate
    };
    const article = (await this.api
      .put(`/api4/articles/${this.articles[articleIndex].id}`, updateArticle)
      .toPromise()) as any;
    this.getArticles().subscribe(res => {
      this.articles = res;
    });
  }
}
