import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-item',
  templateUrl: './item.component.html',
  styleUrls: ['./item.component.css'],
})
export class ItemComponent implements OnInit {
  @Input()
  itemid;
  item;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.item = await this.api.get(`/api3/items/${this.itemid}`).toPromise();
  }

  async save(item) {
    await this.api.post(`/api3/items`, item).toPromise();
    this.notificationService.alert('Item saved');
    // this.router.navigate(['..'], { relativeTo: this.route });
  }

  async remove(itemid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/items/${itemid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.location.back();
    }
  }

  back() {
    this.location.back();
  }
}
