import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css'],
})
export class MenuComponent implements OnInit {
  @Input()
  menuid;
  menu;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.menu = await this.api.get(`/api3/menus/${this.menuid}`).toPromise();
  }

  async save(menu) {
    await this.api.post(`/api3/menus`, menu).toPromise();
    this.notificationService.alert('Menu saved');
    // this.router.navigate(['..'], { relativeTo: this.route });
  }

  async remove(menuid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/menus/${menuid}`)
        .toPromise()) as any;
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.notificationService.alert(result.message);
      this.location.back();
    }
  }

  back() {
    this.location.back();
  }

  addHours(menu) {
    menu.operatingHours.push({});
  }

  removeHours(menu, operatingHour) {
    menu.operatingHours = menu.operatingHours.filter(o => o !== operatingHour);
  }
}
