import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteModule } from '../../route/route.module';
import { RouteComponent } from '../../route/route/route.component';
import { BrandComponent } from './brands/brand/brand.component';
import { BrandsComponent } from './brands/brands/brands.component';
import { CategoriesComponent } from './categories/categories/categories.component';
import { Categories3Component } from './categories/categories3/categories3.component';
import { CategoryComponent } from './categories/category/category.component';
import { DebugsComponent } from './debugs/debugs.component';
import { FbpageComponent } from './fbpages/fbpage/fbpage.component';
import { FbpagesComponent } from './fbpages/fbpages/fbpages.component';
import { InventoryComponent } from './inventory/inventory.component';
import { ItemComponent } from './items/item/item.component';
import { ItemsComponent } from './items/items/items.component';
import { MenuComponent } from './menus/menu/menu.component';
import { MenusComponent } from './menus/menus/menus.component';
import { ModifierComponent } from './modifiers/modifier/modifier.component';
import { ModifiergroupComponent } from './modifiers/modifiergroup/modifiergroup.component';
import { ModifiergroupsComponent } from './modifiers/modifiergroups/modifiergroups.component';
import { ModifiersComponent } from './modifiers/modifiers/modifiers.component';
import { OrderComponent } from './orders/order/order.component';
import { OrdersComponent } from './orders/orders/orders.component';
import { OutletComponent } from './orgs/outlet/outlet.component';
import { OutletsComponent } from './orgs/outlets/outlets.component';
import { BanklogsComponent } from './payment/banklogs/banklogs.component';
import { PaylahsComponent } from './payment/paylahs/paylahs.component';
import { WirecardsComponent } from './payment/wirecards/wirecards.component';
import { ProductComponent } from './products/product/product.component';
import { ProductsComponent } from './products/products/products.component';
import { ProfileComponent } from './profiles/profile/profile.component';
import { ProfilesComponent } from './profiles/profiles/profiles.component';
import { ReferralComponent } from './referrals/referral/referral.component';
import { ReferralsComponent } from './referrals/referrals/referrals.component';
import { SurveyComponent } from './surveys/survey/survey.component';
import { SurveysComponent } from './surveys/surveys/surveys.component';
import { TagComponent } from './tags/tag/tag.component';
import { TagsComponent } from './tags/tags/tags.component';
import { VoucherComponent } from './vouchers/voucher/voucher.component';
import { VouchersComponent } from './vouchers/vouchers/vouchers.component';
import { ArticlesComponent } from './articles/articles/articles.component';
import { ArticleComponent } from './articles/article/article.component';

const routes: Routes = [
  {
    path: 'articles',
    component: ArticlesComponent
  },
  {
    path: 'articles/:articleid',
    component: ArticleComponent
  },
  {
    path: 'items',
    component: ItemsComponent,
  },
  {
    path: 'items/:itemid',
    component: RouteComponent,
    data: { wrappedComponent: ItemComponent },
  },
  {
    path: 'brands',
    component: BrandsComponent,
  },
  {
    path: 'brands/:brandid',
    component: RouteComponent,
    data: { wrappedComponent: BrandComponent },
  },
  {
    path: 'outlets',
    component: OutletsComponent,
  },
  {
    path: 'outlets/:outletid',
    component: RouteComponent,
    data: { wrappedComponent: OutletComponent },
  },
  {
    path: 'menus',
    component: MenusComponent,
  },
  {
    path: 'menus/:menuid',
    component: RouteComponent,
    data: { wrappedComponent: MenuComponent },
  },

  {
    path: 'categories',
    component: CategoriesComponent,
  },
  {
    path: 'categories3',
    component: Categories3Component,
  },
  {
    path: 'categories/:categoryid',
    component: RouteComponent,
    data: { wrappedComponent: CategoryComponent },
  },
  {
    path: 'products',
    component: ProductsComponent,
  },
  {
    path: 'products/:productid',
    component: RouteComponent,
    data: { wrappedComponent: ProductComponent },
  },
  {
    path: 'profiles',
    component: ProfilesComponent,
  },
  {
    path: 'profiles/:pageid/:fbid',
    component: RouteComponent,
    data: { wrappedComponent: ProfileComponent },
  },
  {
    path: 'modifiergroups',
    component: ModifiergroupsComponent,
  },
  {
    path: 'modifiergroups/:modifiergroupid',
    component: RouteComponent,
    data: { wrappedComponent: ModifiergroupComponent },
  },
  {
    path: 'modifiers',
    component: ModifiersComponent,
  },
  {
    path: 'modifiers/:modifierid',
    component: RouteComponent,
    data: { wrappedComponent: ModifierComponent },
  },
  {
    path: 'tags',
    component: TagsComponent,
  },
  {
    path: 'tags/:tagid',
    component: RouteComponent,
    data: { wrappedComponent: TagComponent },
  },
  {
    path: 'fbpages',
    component: FbpagesComponent,
  },
  {
    path: 'fbpages/:pageid',
    component: RouteComponent,
    data: { wrappedComponent: FbpageComponent },
  },
  {
    path: 'orders',
    component: OrdersComponent,
  },
  {
    path: 'orders/:orderid',
    component: RouteComponent,
    data: { wrappedComponent: OrderComponent },
  },
  {
    path: 'referrals',
    component: ReferralsComponent,
  },
  {
    path: 'referrals/:referralid',
    component: RouteComponent,
    data: { wrappedComponent: ReferralComponent },
  },
  {
    path: 'surveys',
    component: SurveysComponent,
  },
  {
    path: 'surveys/:surveyid',
    component: RouteComponent,
    data: { wrappedComponent: SurveyComponent },
  },
  {
    path: 'vouchers',
    component: VouchersComponent,
  },
  {
    path: 'vouchers/:voucherid',
    component: RouteComponent,
    data: { wrappedComponent: VoucherComponent },
  },
  {
    path: 'inventory',
    component: InventoryComponent,
  },
  {
    path: 'banklogs',
    component: BanklogsComponent,
  },
  {
    path: 'paylahs',
    component: PaylahsComponent,
  },
  {
    path: 'wirecards',
    component: WirecardsComponent,
  },
  {
    path: 'debugs',
    component: DebugsComponent,
    runGuardsAndResolvers: 'always',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes), RouteModule],
  exports: [RouterModule],
  entryComponents: [
    ItemComponent,
    BrandComponent,
    OutletComponent,
    ProductComponent,
    ProfileComponent,
    ModifiergroupComponent,
    ModifierComponent,
    TagComponent,
    CategoryComponent,
    FbpageComponent,
    OrderComponent,
    ReferralComponent,
    SurveyComponent,
    VoucherComponent,
    MenuComponent,
  ],
})
export class CrudRoutingModule { }
