import { Pipe, PipeTransform } from '@angular/core';
import { DateTimeFormatter, Instant, LocalDateTime } from 'js-joda';
import * as timeago from 'timeago.js';

// console.log(dayjs().format('YYYY-MM-DD HH:mm:ss'));
const DATE_FORMATTER = DateTimeFormatter.ofPattern('yyyy-MM-dd HH:mm:ss');

@Pipe({
  name: 'asDate',
  pure: true,
})
export class DatePipe implements PipeTransform {
  transform(value: any, args?: any): any {
    try {
      if (value) {
        // const luxonDateTime = DateTime.fromJSDate(value);
        // const luxonDateTime = DateTime.fromISO(value);
        // return (
        //   `${luxonDateTime.toFormat('yyyy-LL-dd HH:mm')}` +
        //   `(${distanceInWordsToNow(value, { addSuffix: true })})`
        // );
        // const momentDate = moment(value);
        // return (
        //   `${momentDate.format('YYYY-MM-DD HH:mm:ss')}` + `(${momentDate.fromNow()})`
        // );
        //
        // ` ` +
        // `(${distanceInWordsToNow(value, { addSuffix: true })})`
        // const date = dayjs(value);
        // `${date.format('YYYY-MM-DD HH:mm:ss')}` +

        const inst = Instant.parse(value);
        const jd = LocalDateTime.ofInstant(inst);
        const formatted = DATE_FORMATTER.format(jd);
        return `${formatted} (${timeago.format(value)})`;
      }
    } catch (error) {
      console.error(error);
    }
    return '-';
  }
}
