import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from 'src/app/auth/auth.service';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-categories3',
  templateUrl: './categories3.component.html',
  styleUrls: ['./categories3.component.css'],
})
export class Categories3Component implements OnInit {
  rootcategories;

  constructor(
    private auth: AuthService,
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.rootcategories = await this.api.get(`/api3/categories`).toPromise();
  }

  async save(category) {
    const result = await this.api
      .post(`/api3/categories`, category)
      .toPromise();
    console.log(result);
    return result;
    // this.notificationService.alert('Category saved');
  }

  async remove(categoryid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/categories/${categoryid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      // this.router.navigate(['..'], { relativeTo: this.route });
      // this.triggerFetch();
    }
  }

  async createCategory() {
    const code = Date.now().toString(36);
    const newCategory = (await this.save({
      name: `New Category ${code}`,
      description: `New Category ${code} Description`,
      imageUrl: ``,
    })) as any;
    this.router.navigate([newCategory.id], { relativeTo: this.activatedroute });
    // this.triggerFetch();
  }
}
