import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  share,
  startWith,
  switchMap,
} from 'rxjs/operators';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css'],
})
export class ProductsComponent implements OnInit {
  searchTerm = new FormControl();
  products$ = this.searchTerm.valueChanges.pipe(
    startWith(''),
    debounceTime(300),
    distinctUntilChanged(),
    switchMap((term: string) => this.api.get(`/api3/products?name=${term}`)),
    share()
  ) as Observable<any[]>;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {}

  async copyProduct(product) {
    const newProduct = (await this.api
      .post(`/api3/products`, {
        ...product,
        id: undefined,
        sku: `${product.sku}-${Date.now().toString(36)}`,
        name: `Copy of ${product.name}`,
      })
      .toPromise()) as any;
    this.router.navigate([newProduct.id], {
      relativeTo: this.activatedroute,
    });
  }

  async createProduct() {
    const code = Date.now().toString(36);
    const newProduct = (await this.api
      .post(`/api3/products`, {
        code,
        sku: code,
        name: `New Product ${code}`,
        imageUrl: ``,
      })
      .toPromise()) as any;
    this.router.navigate([newProduct.id], {
      relativeTo: this.activatedroute,
    });
  }
}
