import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { of, Subject } from 'rxjs';
import {
  catchError,
  debounceTime,
  distinctUntilChanged,
  share,
  switchMap,
  tap,
} from 'rxjs/operators';
import { ApiService } from '../../../shared/api.service';

@Component({
  selector: 'app-tagselect',
  templateUrl: './tagselect.component.html',
  styleUrls: ['./tagselect.component.css'],
})
export class TagselectComponent implements OnInit {
  @Input()
  values;
  @Output()
  valuesChange: EventEmitter<any> = new EventEmitter();

  tagSearch$ = new Subject<string>();
  tags$;
  loading = false;
  selectedTags;

  constructor(private api: ApiService) {}

  ngOnInit() {
    this.tags$ = this.tagSearch$.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      tap(() => (this.loading = true)),
      switchMap((term: string) =>
        this.api.get(`/api3/tags?name=${term}`).pipe(
          catchError(() => of([])), // empty list on error
          tap(() => (this.loading = false))
        )
      ),
      share()
    );

    setTimeout(() => {
      this.tagSearch$.next('');
    });
  }

  addTag = async name => {
    this.loading = true;
    const newTag = await this.api
      .post(`/api3/tags`, {
        name,
      })
      .toPromise();
    this.loading = false;
    return newTag;
  };

  onChange(events) {
    this.valuesChange.emit(events);
  }
}
