import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  share,
  startWith,
  switchMap,
} from 'rxjs/operators';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-modifiergroups',
  templateUrl: './modifiergroups.component.html',
  styleUrls: ['./modifiergroups.component.css'],
})
export class ModifiergroupsComponent implements OnInit {
  searchTerm = new FormControl();
  modifiergroups$ = this.searchTerm.valueChanges.pipe(
    startWith(''),
    debounceTime(300),
    distinctUntilChanged(),
    switchMap((term: string) =>
      this.api.get(`/api3/modifiergroups?name=${term}`)
    ),
    share()
  ) as Observable<any[]>;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {}

  async createModifiergroup() {
    const newOrg = (await this.api
      .post(`/api3/modifiergroups`, {
        code: `${Date.now().toString(36)}`,
        name: `New Group ${Date.now().toString(36)}`,
      })
      .toPromise()) as any;
    this.router.navigate([newOrg.id], {
      relativeTo: this.activatedroute,
    });
  }
}
