import { Location } from '@angular/common';
import { Component, ElementRef, Input, NgZone, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-categorybranch',
  templateUrl: './categorybranch.component.html',
  styleUrls: ['./categorybranch.component.css'],
})
export class CategorybranchComponent implements OnInit {
  @Input()
  categoryid;
  category;

  @Input()
  depth;

  showDetails = true;

  // private fetchTrigger = new BehaviorSubject(null);
  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location,
    private element: ElementRef,
    private zone: NgZone
  ) {}

  async ngOnInit() {
    this.category = await this.api
      .get(`/api3/categories/${this.categoryid}`)
      .toPromise();
    // this.category$ = this.fetchTrigger.pipe(
    //   switchMap(() => this.api.get(`/api3/categories/${this.categoryid}`))
    // );
    // this.triggerFetch();
  }

  async copyCategory(category) {
    const newCategory = (await this.api
      .post(`/api3/categories`, {
        ...category,
        id: undefined,
        name: `Copy of ${category.name}`,
        products: [],
      })
      .toPromise()) as any;

    for (const product of category.products) {
      const newProduct = await this.api
        .post(`/api3/products`, {
          ...product,
          id: undefined,
          name: `Copy of ${product.name}`,
          category: newCategory.id,
        })
        .toPromise();
    }

    this.router.navigate([newCategory.id], { relativeTo: this.activatedroute });
  }
  async save(category) {
    await this.api.post(`/api3/categories`, category).toPromise();
    // this.notificationService.alert('Category saved');
  }

  async remove(categoryid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/categories/${categoryid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      this.category = undefined;
    }
  }

  // triggerFetch() {
  //   this.fetchTrigger.next(null);
  // }
}
