import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';
@Component({
  selector: 'app-modifiergroup',
  templateUrl: './modifiergroup.component.html',
  styleUrls: ['./modifiergroup.component.css'],
})
export class ModifiergroupComponent implements OnInit {
  @Input()
  modifiergroupid;
  modifiergroup;
  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.modifiergroup = await this.api
      .get(`/api3/modifiergroups/${this.modifiergroupid}`)
      .toPromise();
  }

  async save(modifiergroup) {
    await this.api.post(`/api3/modifiergroups`, modifiergroup).toPromise();
    this.notificationService.alert('modifiergroup saved');
    // this.router.navigate(['..'], { relativeTo: this.route });
  }

  async remove(modifiergroupid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/modifiergroups/${modifiergroupid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.location.back();
    }
  }

  addItem(array) {
    array.push({ modifier: {} });
  }

  removeItem(array, index) {
    array.splice(index, 1);
  }

  back() {
    this.location.back();
  }
}
