import { DOCUMENT } from '@angular/common';
import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Inject,
  Input,
  NgZone,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { config } from 'src/environments/config';
import { loadScript } from '../../../libs/htmlutlis';

declare const google: any;

@Component({
  selector: 'app-maps',
  templateUrl: './maps.component.html',
  styleUrls: ['./maps.component.css'],
})
export class MapsComponent implements OnInit, AfterViewInit {
  @ViewChild('container')
  container: ElementRef;
  map;

  @Input()
  latlng;
  @Output()
  latlngChange = new EventEmitter();

  constructor(private zone: NgZone, @Inject(DOCUMENT) private document: any) {}

  ngOnInit() {
    // if (this.latlng === null) {
    //   this.latlng = { lat: 1.35, lng: 103.82 };
    //   this.emit();
    // }
  }

  async ngAfterViewInit() {
    if (!window['google']) {
      await loadScript(
        `https://maps.googleapis.com/maps/api/js?key=${config.googleMaps}`
      );
      this.initMap();
      // const script = document.createElement('script');
      // script.src = `https://maps.googleapis.com/maps/api/js?key=${
      //   config.googleMaps
      // }`;
      // script.onload = () => {
      //   this.initMap();
      // };
      // document.head.appendChild(script);
    } else {
      this.initMap();
    }
  }

  initMap() {
    // const position = this.latlng ? this.latlng : { lat: 1.35, lng: 103.82 };
    this.map = new google.maps.Map(this.container.nativeElement, {
      center: this.latlng ? this.latlng : { lat: 1.35, lng: 103.82 },
      zoom: 12,
    });
    const marker = new google.maps.Marker({
      position: this.latlng, // can be undefined
      map: this.map,
    });
    // const marker = new google.maps.Marker({ position, map: this.map });
    this.map.addListener('click', e => {
      this.zone.run(() => {
        this.latlng = e.latLng;
        marker.setPosition(e.latLng);
        this.emit();
      });
    });
  }

  emit() {
    this.latlngChange.emit(this.latlng);
  }
}
