import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from 'src/app/auth/auth.service';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css'],
})
export class ProductComponent implements OnInit {
  @Input()
  productid;
  product;

  categories;

  account;

  constructor(
    private auth: AuthService,
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.account = this.auth.getAccount();
    this.product = await this.api
      .get(`/api3/products/${this.productid}`)
      .toPromise();
    this.categories = await this.api.get(`/api3/categories`).toPromise();
  }

  async save(product) {
    await this.api.post(`/api3/products`, product).toPromise();
    // await this.http
    //   .put(`/api3/products/${this.productid}`, product)
    //   .toPromise();
    this.notificationService.alert('Product saved');
    // this.router.navigate(['..'], { relativeTo: this.route });
  }

  async remove(productid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/products/${productid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.location.back();
    }
  }

  back() {
    this.location.back();
  }

  compareFn(x, y) {
    return x && y && x.id === y.id;
  }
}
