import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  share,
  startWith,
  switchMap,
} from 'rxjs/operators';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-outlets',
  templateUrl: './outlets.component.html',
  styleUrls: ['./outlets.component.css'],
})
export class OutletsComponent implements OnInit {
  searchTerm = new FormControl();
  outlets$ = this.searchTerm.valueChanges.pipe(
    startWith(''),
    debounceTime(300),
    distinctUntilChanged(),
    switchMap((term: string) => this.api.get(`/api3/outlets?name=${term}`)),
    share()
  ) as Observable<any[]>;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {}

  async createOutlet() {
    const code = Date.now().toString(36);
    const newOutlet = (await this.api
      .post(`/api3/outlets`, {
        name: `New Outlet Name ${code}`,
        description: `New Outlet Description ${code}`,
        imageUrl: ``,
        latlng: { lat: 1.35, lng: 103.82 },
      })
      .toPromise()) as any;
    this.router.navigate([newOutlet.id], {
      relativeTo: this.activatedroute,
    });
  }

  async copyOutlet(outlet) {
    const dbOutlet = await this.api
      .get(`/api3/outlets/${outlet.id}`)
      .toPromise();

    const newOutlet = (await this.api
      .post(`/api3/outlets`, {
        ...outlet,
        id: undefined,
        name: `Copy of ${outlet.name}`,
        categories: outlet.categories,
      })
      .toPromise()) as any;

    this.router.navigate([newOutlet.id], {
      relativeTo: this.activatedroute,
    });
  }
}
