import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-fbpage',
  templateUrl: './fbpage.component.html',
  styleUrls: ['./fbpage.component.css'],
})
export class FbpageComponent implements OnInit {
  @Input()
  pageid;
  page;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.page = await this.api.get(`/api3/fbpages/${this.pageid}`).toPromise();
  }

  async save(page) {
    await this.api.post(`/api3/fbpages`, page).toPromise();
    this.notificationService.alert('Page saved');
    // this.router.navigate(['..'], { relativeTo: this.route });
  }

  async remove(pageid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/fbpages/${pageid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.location.back();
    }
  }

  back() {
    this.location.back();
  }
}
