import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-outlet',
  templateUrl: './outlet.component.html',
  styleUrls: ['./outlet.component.css'],
})
export class OutletComponent implements OnInit {
  @Input()
  outletid;
  outlet;

  fbpages;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.outlet = await this.api
      .get(`/api3/outlets/${this.outletid}`)
      .toPromise();
    this.fbpages = await this.api.get(`/api3/fbpages`).toPromise();
  }

  async save(outlet) {
    await this.api.post(`/api3/outlets`, outlet).toPromise();
    this.notificationService.alert('Outlet saved');
    // this.router.navigate(['..'], { relativeTo: this.route });
  }

  async remove(outletid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/outlets/${outletid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.location.back();
    }
  }

  back() {
    this.location.back();
  }

  addOperatingHours(outlet) {
    outlet.operatingHours.push({});
  }

  removeOperatingHours(outlet, operatingHour) {
    outlet.operatingHours = outlet.operatingHours.filter(
      o => o !== operatingHour
    );
  }

  compareFn(x, y) {
    return x && y && x.id === y.id;
  }

  trackByIdx(index: number, obj: any): any {
    return index;
  }
}
