import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css'],
})
export class CategoryComponent implements OnInit {
  @Input()
  categoryid;
  category;

  // outlets;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    // this.category$ = this.http.get(`/api3/categories/${this.categoryid}`);
    this.category = await this.api
      .get(`/api3/categories/${this.categoryid}`)
      .toPromise();
    // this.outlets = await this.api.get(`/api3/outlets`).toPromise();
  }

  async save(category) {
    await this.api.post(`/api3/categories`, category).toPromise();
    this.notificationService.alert('Category saved');
  }

  async remove(categoryid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/categories/${categoryid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.location.back();
    }
  }

  compareFn(x, y) {
    return x && y && x.id === y.id;
  }

  back() {
    this.location.back();
  }

  async createProduct(category) {
    const code = Date.now().toString(36);
    const newProduct = (await this.api
      .post(`/api3/products`, {
        code,
        sku: code,
        name: `New Product ${code}`,
        imageUrl: ``,
        category,
        sort: category.products.length + 1,
      })
      .toPromise()) as any;
    this.router.navigate(['../..', 'products', newProduct.id], {
      relativeTo: this.activatedroute,
    });
  }
}
