import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { of, Subject } from 'rxjs';
import {
  catchError,
  debounceTime,
  distinctUntilChanged,
  map,
  share,
  switchMap,
  tap,
} from 'rxjs/operators';
import { ApiService } from '../../../shared/api.service';

@Component({
  selector: 'app-modifierselect',
  templateUrl: './modifierselect.component.html',
  styleUrls: ['./modifierselect.component.css'],
})
export class ModifierselectComponent implements OnInit {
  @Input()
  values;
  @Output()
  valuesChange: EventEmitter<any> = new EventEmitter();

  modifierSearch$ = new Subject<string>();
  modifiers$;
  loading = false;
  selectedmodifiers;

  constructor(private api: ApiService) {}

  ngOnInit() {
    this.modifiers$ = this.modifierSearch$.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      tap(() => (this.loading = true)),
      switchMap((term: string) =>
        this.api.get(`/api3/modifiers?name=${term}`).pipe(
          catchError(() => of([])), // empty list on error
          tap(() => (this.loading = false))
        )
      ),
      map(modifiers => (modifiers as any).filter(modifier => modifier.active)),
      share()
    );

    setTimeout(() => {
      this.modifierSearch$.next('');
    });
  }

  addModifier = async name => {
    this.loading = true;
    const newModifier = await this.api
      .post(`/api3/modifiers`, {
        code: Date.now().toString(36),
        name,
        modifier: [],
      })
      .toPromise();
    this.loading = false;
    return newModifier;
  };

  onChange(events) {
    this.valuesChange.emit(events);
  }
}
