import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { Component, OnInit, ViewEncapsulation, Input } from '@angular/core';
import { ArticleService } from '../../../article/article.service';
import { AngularFireStorage } from '@angular/fire/storage';
import { ApiService } from 'src/app/shared/api.service';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { MatChipInputEvent } from '@angular/material';
import { Observable } from 'rxjs';
@Component({
  selector: 'app-article',
  templateUrl: './article.component.html',
  styleUrls: ['./article.component.css'],
  encapsulation: ViewEncapsulation.None
})

export class ArticleComponent implements OnInit {
  articles: Object[];
  title = '';
  startDate = '';
  endDate = '';
  htmlText = '';
  previewImage = '';
  thumbnailImage = '';
  tags: string[] = [];
  sort = null;
  quillEditorRef = null;
  articleid: string | number;

  setTopArticle = false;
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];

  constructor(
    private api: ApiService,
    private http: HttpClient,
    public location: Location,
    private route: ActivatedRoute,
    private storage: AngularFireStorage,
    private articleService: ArticleService
  ) { }

  ngOnInit() {
    this.articleid = this.route.snapshot.paramMap.get('articleid');
    if (this.articleid !== 'new') {
      this.getArticle(this.articleid).subscribe((data) => {
        this.title = data.title;
        this.startDate = data.startDate;
        this.endDate = data.endDate;
        this.htmlText = data.content;
        this.previewImage = data.previewImage;
        this.thumbnailImage = data.thumbnailImage;
        this.tags = data.tags;
        this.articles = JSON.parse(this.articleService.getArticles());
        for (let i = 0; i < this.articles.length; i++) {
          if (this.articles[i]['sort'] === data.sort) {
            this.sort = i + 1;
          }
        }
      });
    }
  }

  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    if ((value || '').trim()) {
      this.tags.push(value.trim());
    }
    if (input) {
      input.value = '';
    }
  }

  remove(tag: string): void {
    const index = this.tags.indexOf(tag);

    if (index >= 0) {
      this.tags.splice(index, 1);
    }
  }

  getArticles(id: string): Observable<any> {
    return this.http.get<any>(`/api3/articles`);
  }

  getArticle(id: string): Observable<any> {
    return this.http.get<any>(`/api3/articles/${id}`);
  }

  getEditorInstance(editorInstance: any) {
    this.quillEditorRef = editorInstance;
    const toolbar = editorInstance.getModule('toolbar');
    toolbar.addHandler('image', this.imageHandler);
  }

  setTopSort() {
    this.setTopArticle = true;
  }

  imageHandler = (image: any, callback: any) => {
    document.getElementById('input-file-hidden').click();
  };

  uploadImage(event: any, type: string) {
    const id: string = Math.random().toString(36).substr(2, 9);
    const file = event.target.files[0];
    const filePath = id;
    this.storage.upload(filePath, file).then(() => {
      const ref = this.storage.ref(filePath);
      ref.getDownloadURL().subscribe(url => {
        if (type === 'content') {
          const range = this.quillEditorRef.getSelection();
          const img = `<img src="${url}"/>`;
          this.quillEditorRef.clipboard.dangerouslyPasteHTML(range.index, img);
          this.htmlText = document.getElementsByClassName('ql-editor')[0].innerHTML;
        } else if (type === 'preview') {
          this.previewImage = url;
        } else if (type === 'thumbnail') {
          this.thumbnailImage = url;
        }
      });
    });
  }

  articlePreview() {
    this.articleService.setArticleHtmlText(this.htmlText);
  }

  async createArticle() {
    const textContent = document.getElementsByClassName('ql-editor')[0].textContent;
    const body = {
      title: this.title,
      subtitle: textContent,
      previewImage: this.previewImage,
      thumbnailImage: this.thumbnailImage,
      content: this.htmlText,
      tags: this.tags,
      startDate: this.startDate,
      endDate: this.endDate
    };
    const newArticle = (await this.api
      .post(`/api4/articles`, body)
      .toPromise()) as any;
    this.location.back();
  }

  async updateArticle() {
    const textContent = document.getElementsByClassName('ql-editor')[0].textContent;
    let sort = this.articles[this.sort - 1]['sort'];
    if (this.setTopArticle) {
      sort = this.articles[0]['sort'];
    }
    const updateArticle = {
      title: this.title,
      subtitle: textContent,
      previewImage: this.previewImage,
      thumbnailImage: this.thumbnailImage,
      content: this.htmlText,
      tags: this.tags,
      startDate: this.startDate,
      endDate: this.endDate,
      sort: sort
    };
    const article = (await this.api
      .put(`/api4/articles/${this.articleid}`, updateArticle)
      .toPromise()) as any;
    this.location.back();
  }
}
