import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css'],
})
export class ProfileComponent implements OnInit {
  @Input()
  pageid;

  @Input()
  fbid;

  profile;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.profile = await this.api
      .get(`/api3/profiles/${this.pageid}/${this.fbid}`)
      .toPromise();
  }

  async save(profile) {
    await this.api.post(`/api3/profiles`, profile).toPromise();
    this.notificationService.alert('Profile saved');
    // this.router.navigate(['..'], { relativeTo: this.route });
  }

  async remove(profileid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/profiles/${profileid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.location.back();
    }
  }

  back() {
    this.location.back();
  }
}
