import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css'],
})
export class OrderComponent implements OnInit {
  @Input()
  orderid;
  order;
  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.order = await this.api.get(`/api3/orders/${this.orderid}`).toPromise();
  }

  async save(order) {
    await this.api.post(`/api3/orders`, order).toPromise();
    this.notificationService.alert('Order saved');
    // this.router.navigate(['..'], { relativeTo: this.route });
  }

  async remove(orderid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/orders/${orderid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.location.back();
    }
  }

  renderModifiers = cartitemmodifier => {
    if (!cartitemmodifier) {
      return '';
    }
    const results = [];
    const keys = Object.keys(cartitemmodifier);
    for (const key of keys) {
      const modifiers = cartitemmodifier[key];
      for (const modifier of modifiers) {
        results.push(modifier.modifier.name);
      }
    }
    return results.join(', ');
  };

  back() {
    this.location.back();
  }
}
