import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  share,
  startWith,
  switchMap,
} from 'rxjs/operators';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-modifiers',
  templateUrl: './modifiers.component.html',
  styleUrls: ['./modifiers.component.css'],
})
export class ModifiersComponent implements OnInit {
  searchTerm = new FormControl();
  modifiers$ = this.searchTerm.valueChanges.pipe(
    startWith(''),
    debounceTime(300),
    distinctUntilChanged(),
    switchMap((term: string) => this.api.get(`/api3/modifiers?name=${term}`)),
    share()
  ) as Observable<any[]>;
  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {}

  async createModifier() {
    const code = Date.now().toString(36);
    const newOrg = (await this.api
      .post(`/api3/modifiers`, {
        code,
        name: `New Modifier ${code}`,
        sku: code,
        price: 0,
      })
      .toPromise()) as any;
    this.router.navigate([newOrg.id], {
      relativeTo: this.activatedroute,
    });
  }
}
