import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'ellipsis',
  pure: true,
})
export class EllipsisPipe implements PipeTransform {
  transform(value: string, size = 30): any {
    if (value.length > size) {
      return value.substring(0, size - 4) + ' ...';
    }
    return value;
  }
}
