import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  share,
  startWith,
  switchMap,
} from 'rxjs/operators';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-fbpages',
  templateUrl: './fbpages.component.html',
  styleUrls: ['./fbpages.component.css'],
})
export class FbpagesComponent implements OnInit {
  searchTerm = new FormControl();
  pages$ = this.searchTerm.valueChanges.pipe(
    startWith(''),
    debounceTime(300),
    distinctUntilChanged(),
    switchMap((term: string) => this.api.get(`/api3/fbpages?name=${term}`)),
    share()
  ) as Observable<any[]>;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {}

  async createFbpage() {
    const code = Date.now().toString(36);
    const newPage = (await this.api
      .post(`/api3/fbpages`, {
        name: `${code}`,
        imageUrl: ``,
      })
      .toPromise()) as any;
    this.router.navigate([newPage.id], {
      relativeTo: this.activatedroute,
    });
  }
}
