import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { of, Subject } from 'rxjs';
import {
  catchError,
  debounceTime,
  distinctUntilChanged,
  share,
  switchMap,
  tap,
} from 'rxjs/operators';
import { ApiService } from '../../../shared/api.service';

@Component({
  selector: 'app-menuselect',
  templateUrl: './menuselect.component.html',
  styleUrls: ['./menuselect.component.css'],
})
export class MenuselectComponent implements OnInit {
  @Input()
  values;
  @Output()
  valuesChange: EventEmitter<any> = new EventEmitter();

  menuSearch$ = new Subject<string>();
  menus$;
  loading = false;
  selectedMenu;

  constructor(private api: ApiService) {}

  ngOnInit() {
    this.menus$ = this.menuSearch$.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      tap(() => (this.loading = true)),
      switchMap((term: string) =>
        this.api.get(`/api3/menus?name=${term}`).pipe(
          catchError(() => of([])), // empty list on error
          tap(() => (this.loading = false))
        )
      ),
      share()
    );

    setTimeout(() => {
      this.menuSearch$.next('');
    });
  }

  onChange(events) {
    this.valuesChange.emit(events);
  }

  onClear() {
    this.valuesChange.emit(null);
  }
}
