import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../../shared/api.service';

@Component({
  selector: 'app-brand',
  templateUrl: './brand.component.html',
  styleUrls: ['./brand.component.css'],
})
export class BrandComponent implements OnInit {
  @Input()
  brandid;
  brand;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.brand = await this.api.get(`/api3/brands/${this.brandid}`).toPromise();
  }

  async save(brand) {
    await this.api.post(`/api3/brands`, brand).toPromise();
    this.notificationService.alert('Brand saved');
    // this.router.navigate(['..'], { relativeTo: this.route });
  }

  async remove(brandid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/brands/${brandid}`)
        .toPromise()) as any;
      this.notificationService.alert(result.message);
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.location.back();
    }
  }

  back() {
    this.location.back();
  }
}
