import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  share,
  startWith,
  switchMap,
} from 'rxjs/operators';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../shared/api.service';

@Component({
  selector: 'app-debugs',
  templateUrl: './debugs.component.html',
  styleUrls: ['./debugs.component.css'],
})
export class DebugsComponent implements OnInit {
  searchTerm = new FormControl();
  debugs$ = this.searchTerm.valueChanges.pipe(
    startWith(''),
    debounceTime(300),
    distinctUntilChanged(),
    switchMap((term: string) => this.api.get(`/api3/debugs?url=${term}`)),
    share()
  ) as Observable<any[]>;
  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {}

  async remove(debugid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api3/debugs/${debugid}`)
        .toPromise()) as any;
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.notificationService.alert(result.message);
      // this.location.back();
    }
  }
}
